.text--primary {
  color: $primary;
}
.text--secondary {
  color: $secondary;
}
.text--success {
  color: $success;
}
.text--danger {
  color: $danger;
}
.text--warning {
  color: $warning;
}
.text--info {
  color: $info;
}
.text--dark {
  color: $dark;
}
.text--white {
  @include light-clr;
}
.text-clr {
  color: $text-clr;
}
.heading-clr {
  color: $heading-clr;
}
.text--base {
  @include base-clr;
}
.text--accent {
  color: $accent-clr;
}